import React, { Component } from "react";
import Message from "../Message";
import "./Chat.css";

class Chat extends Component {
  state = {
    messages: [],
    inputMsg: ""
  };
  changeMessage = event => {
    this.setState({ inputMsg: event.target.value });
  };
  sendMessage = event => {
    if (event.key === "Enter" && this.state.inputMsg.trim().length) {
      this.setState(({ messages, inputMsg }) => ({
        inputMsg: "",
        messages: [...messages, { text: inputMsg }]
      }));
    }
  };
  render() {
    const { messages, inputMsg } = this.state;
    return (
      <div className="chat">
        <div className="message-list">
          <div className="messages">
            {messages.map((item, key) => (
              <Message text={item.text} key={key} />
            ))}
          </div>
        </div>
        <input
          className="input-message"
          value={inputMsg}
          onChange={this.changeMessage}
          onKeyPress={this.sendMessage}
        />
      </div>
    );
  }
}

export default Chat;
